<!-- eslint-disable -->
<template>
  <main class="content container" v-if="productLoading">Идет загрузка товара</main>
  <main class="content container" v-else-if="!productData">Не удалось загрузить товар</main>
  <!-- ГРУЗИМ ЭТУ ЧАСТЬ ШАБЛОНА ЕСЛИ ТОЕЛЬКО ЕСТЬ ИНФОРМАЦИЯ О ТОВАРЕ -->
  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            {{ category.title }}
          </router-link>
          <!-- Второй вариант записи ссылки импользуется в scripts-->
          <!-- <a class="breadcrumbs__link" @click.prevent="$router.push({name: 'main'})">
            {{ category.title }}
          </a> -->
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            {{ product.title }}
          </a>
        </li>
      </ul>
    </div>
    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img width="570" height="570" :src="product.image.file.url" :alt="product.title">
        </div>
        <ul class="pics__list">
          <li class="pics__item">
            <a href="" class="pics__link pics__link--current">
              <img width="98" height="98" :src="product.image.file.url" :alt="product.title">
            </a>
          </li>
        </ul>
      </div>

      <div class="item__info">
        <span class="item__code">Артикул: {{ product.id }}</span>
        <h2 class="item__title">
          {{ product.title }}
        </h2>
        <div class="item__form">

          <form class="form" action="#" method="POST" @submit.prevent="addToCart()">
            <b class="item__price">
              <!-- Применяем функцию из фильтра -->
              {{ product.price | numberFormat }} ₽
            </b>

            <fieldset class="form__block">
              <legend class="form__legend">Цвет:</legend>
              <ul class="colors">
                <li class="colors__item" v-for="color in product.colors" :key="color.id">
                  <label class="colors__label" >
                    <input class="colors__radio sr-only" type="radio" :value="color.title">
                    <span class="colors__value" :style="'background-color: ' + color.code">
                    </span>

                  </label>
                </li>
              </ul>
            </fieldset>

            <div class="item__row">

              <BaseChanger :amount-initial.sync="productAmount"/>

              <button class="button button--primery" type="submit" :disabled="productAddSending">
                В корзину
              </button>
            </div>
            <div v-show="productAdded">Товар добавлен в корзину</div>
            <div v-show="productAddSending">Товар отправляется в корзину</div>
          </form>
        </div>
      </div>
      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a class="tabs__link tabs__link--current">
              Описание
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              Характеристики
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              Гарантия
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              Оплата и доставка
            </a>
          </li>
        </ul>
        <div class="item__content">
          <p>
            Навигация GPS, ГЛОНАСС, BEIDOU Galileo и QZSS<br>
            Синхронизация со смартфоном<br>
            Связь по Bluetooth Smart, ANT+ и Wi-Fi<br>
            Поддержка сторонних приложений<br>
          </p>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import { mapActions } from 'vuex';
import numberFormat from '@/helpers/numberFormat';
import BaseChanger from '@/components/BaseChanger.vue';

export default {
  data() {
    return {
      productAmount: 1,
      productData: null,
      productLoading: false,
      productLoadingFailed: false,
      productAdded: false,
      productAddSending: false,
    };
  },
  components: {
    BaseChanger,
  },
  filters: {
    numberFormat,
  },
  computed: {
    product() {
      // Предаем параметр из this.$route, который приводится к числу +this.$route
      return this.productData;
    },
    category() {
      return this.productData.category;
    },
  },
  methods: {
    ...mapActions(['addProductToCart']),

    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;
      // Вместо мутации вызываем действие - dispatch
      // this.$store.dispatch(
      //   'addProductToCart',
      //   { productId: this.product.id, amount: this.productAmount },
      // );
      // Если исполлзуем проксирование
      this.addProductToCart({ productId: this.product.id, amount: this.productAmount })
        .then(() => {
          this.productAdded = true;
          this.productAddSending = false;
        });
    },

    loadProduct() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      axios.get(`${API_BASE_URL}/api/products/${this.$route.params.id}`)
        .then((response) => { this.productData = response.data; })
        .catch(() => { this.productLoadingFailed = true; })
        .then(() => { this.productLoading = false; });
    },

  },

  // Наблюдатель на изменение динамического сегмента на создание компонента
  watch: {
    '$route.params.id': {
      handler() {
        this.loadProduct();
      },
      immediate: true,
    },
  },
};

</script>
