<!-- eslint-disable -->
<template>
  <main class="content container" v-if="orderLoading">Идет загрузка информации о заказе</main>
  <main class="content container" v-else-if="orderLoadingFailed">Не удалось получить информацию по заказу</main>
  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'order'}">
            Оформление заказа
          </router-link>
        </li>
      </ul>
      <!-- v-if="pageOrderInfo" нужен чтобы убрать ошибки асинхронности -->
      <h1 class="content__title" v-if="pageOrderInfo">
        Заказ оформлен <span>№ {{ pageOrderInfo.id }} </span>
      </h1>
    </div>

    <section class="cart" v-if="pageOrderInfo">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа.
            Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                Получатель
              </span>
              <span class="dictionary__value" >
                {{ pageOrderInfo.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Адрес доставки
              </span>
              <span class="dictionary__value">
                {{ pageOrderInfo.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Телефон
              </span>
              <span class="dictionary__value">
                {{ pageOrderInfo.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ pageOrderInfo.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Способ оплаты
              </span>
              <span class="dictionary__value">
                картой при получении
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order"  v-for="item in pageOrderInfo.basket.items" :key="item.id">
              <h3>{{ item.product.title }}</h3>
              <b>{{ item.price | numberFormat }} ₽</b>
              <span>Артикул: {{ item.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{ orderTotalProdQuantity }}</b> товара на сумму
             <b>{{ pageOrderInfo.totalPrice | numberFormat }} ₽</b></p>
            <p>Статус заказа: <b>{{ pageOrderInfo.status.title }}</b></p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';

export default {
  data() {
    return {
      orderLoading: false,
      orderLoadingFailed: false,
    };
  },
  filters: { numberFormat },
  computed: {
    pageOrderInfo() {
      return this.$store.state.orderInfo;
    },
    orderTotalProdQuantity() {
      return this.pageOrderInfo.basket.items.reduce((acc, item) => (item.quantity) + acc, 0);
    },
  },
  watch: {
    '$route.params.id': {
      handler() {
        if (this.$store.state.orderInfo
        && this.$store.state.orderInfo.id === this.$route.params.id) {
          return;
        }
        this.orderLoading = true;
        this.orderLoadingFailed = false;
        (new Promise((resolve) => { setTimeout(resolve, 1000); }))
          .then(() => {
            // Вызываем действие и orderId берем из динамического сегмента роутера
            this.$store.dispatch('loadOrderInfo', this.$route.params.id)
              .then(() => {
                this.orderLoading = false;
                this.orderLoadingFailed = false;
              })
              .catch(() => {
                this.orderLoading = false;
                this.orderLoadingFailed = true;
              });
          });
      },
      immediate: true,
    },
  },
};

</script>
