import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

Vue.use(Vuex);
// Создаем новое хранилище
export default new Vuex.Store({
  state: {
    cartProducts: [],
    // Свойство в котором храниться ключь доступа пользователя к корзине
    userAccessKey: null,
    // Товары в козине по API
    cartProductData: [],

    orderInfo: null,

  },

  mutations: {
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },

    resetCart(state) {
      state.cartProducts = [];
      state.cartProductData = [];
    },

    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },

    updateCartProductAmount(state, { productId, amount }) {
      const item = this.state.cartProducts.find((el) => el.productId === productId);
      if (item) {
        item.amount = amount;
      }
    },

    updateCartProductData(state, item) {
      state.cartProductData = item;
    },

    deleteCartProduct(state, productId) {
      state.cartProducts = state.cartProducts.filter((item) => item.productId !== productId);
    },

    syncCartProducts(state) {
      state.cartProducts = state.cartProductData.map((item) => ({
        productId: item.product.id,
        amount: item.quantity,
      }));
    },

  },

  getters: {
    cartDetailProduct(state) {
      return state.cartProducts.map((item) => {
        const { product } = state.cartProductData.find((p) => p.product.id === item.productId);
        return {
          ...item,
          product: {
            ...product,
            image: product.image.file.url,
          },
        };
      });
    },

    // Второй арогумент геттера, объект со всеми геттерами
    cartTotalPrice(state, getters) {
      return getters.cartDetailProduct
        .reduce((acc, item) => acc + (item.product.price * item.amount), 0);
    },

    cartTotal(state, getters) {
      return getters.cartDetailProduct
        .reduce((acc, item) => (item.amount) + acc, 0);
    },

  },
  // Мутации всегда синхронные по этому есть действия
  // conrext содержит те же методы что и сам экзымпляр глобального хранилища
  actions: {
    loadOrderInfo(conrext, orderId) {
      return axios
        .get(`${API_BASE_URL}/api/orders/${orderId}`, {
          params: {
            userAccessKey: conrext.state.userAccessKey,
          },
        })
        .then((response) => {
          conrext.commit('updateOrderInfo', response.data);
        });
    },
    loadCart(conrext) {
      // Чтобы действие вернуло промис исползуем return
      return axios
        .get(`${API_BASE_URL}/api/baskets`, {
          // Предаем уникальный ключь при послед запросах
          params: {
            userAccessKey: conrext.state.userAccessKey,
          },
        })
        .then((response) => {
          // Если ключа во vuex нет, то получаем его
          if (!conrext.state.userAccessKey) {
            // Записываем ключ в localStorage чтобы он не терялся при обновлении сессии
            localStorage.setItem('userAccessKey', response.data.user.accessKey);
            // Записываем уникальный ключь в vuex
            conrext.commit('updateUserAccessKey', response.data.user.accessKey);
          }
          conrext.commit('updateCartProductData', response.data.items);
          conrext.commit('syncCartProducts');
        });
    },

    addProductToCart(conrext, { productId, amount }) {
      // Чтобы действие вернуло промис исползуем return
      return (new Promise((resolve) => { setTimeout(resolve, 500); }))
        .then(() => axios
          .post(`${API_BASE_URL}/api/baskets/products`, {
            productId,
            quantity: amount,
          }, {
            params: {
              userAccessKey: conrext.state.userAccessKey,
            },
          })
          .then((response) => {
            conrext.commit('updateCartProductData', response.data.items);
            conrext.commit('syncCartProducts');
          }));
    },

    updateCartProductAmount(conrext, { productId, amount }) {
      conrext.commit('updateCartProductAmount', { productId, amount });
      if (amount < 1) {
        return null;
      }
      return axios
        .put(`${API_BASE_URL}/api/baskets/products`, {
          productId,
          quantity: amount,
        }, {
          params: {
            userAccessKey: conrext.state.userAccessKey,
          },
        })
        .then((response) => {
          conrext.commit('updateCartProductData', response.data.items);
        })
        .catch(() => {
          conrext.commit('syncCartProducts');
        });
    },

    deleteCartProduct(conrext, productId) {
      conrext.commit('deleteCartProduct', productId);
      return axios
        .delete(`${API_BASE_URL}/api/baskets/products`, {
          params: { userAccessKey: conrext.state.userAccessKey },
          data: { productId },
        })
        .then((response) => {
          conrext.commit('updateCartProductData', response.data.items);
        })
        .catch(() => {
          conrext.commit('syncCartProducts');
        });
    },

  },

});
