<!-- eslint-disable -->
<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        Товаров - {{ countProducts }}
      </span>
    </div>

    <div class="content__catalog">
      <ProductsFilter
        :price-from.sync="filterPriceFrom"
        :price-to.sync="filterPriceTo"
        :category-id.sync="filterCategoryId"
        :colors-check.sync="filterColorId"
      />
      <section class="catalog">
        <div v-if="productsLoading">Загрузка товаров...</div>
        <div v-if="productsLoadingFailed">Произошла ошибка при загрузке товаров
        <button @click.prevent="loadProducts">Попробовать еще раз</button>
        </div>
        <!-- Добавляем обработчик события gotoPage и снова
        её передаем при этом используем стрелочную ф-ию -->
        <!-- <ProductList :products="products"
          @gotoPage="(pageName, pageParams) => $emit('gotoPage', pageName, pageParams)" /> -->
        <ProductList :products="products" />
        <BasePagination
          v-model="page"
          :count="countProducts"
          :per-page="productsPerPage"
        />
      </section>
    </div>
  </main>
</template>

<script>
// @ - всегда отчитывать путь от SRC!!!
import { API_BASE_URL } from '@/config';
import ProductsFilter from '@/components/ProductsFilter.vue';
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import axios from 'axios';

export default {
  components: {
    ProductsFilter,
    ProductList,
    BasePagination,
  },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColorId: '',
      page: 1,
      productsPerPage: 9,
      productsData: null,
      productsLoading: false,
      productsLoadingFailed: false,
    };
  },
  computed: {
    products() {
      // Так как у нас асинхроность то используем тернарный оператор,
      // который в случае отсутствия данных вернет пустой массив
      // А так же меняем путь подмешивая свойство используя map и ...!!!
      return this.productsData
        ? this.productsData.items.map((product) => ({
          ...product,
          image: product.image.file.url,
        }))
        : [];
    },
    // Так как в API есть раздел pagination
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  methods: {
    // setTimeout и clearTimeout нужны для того чтобы не вызывать несколько раз запросы к серверу
    // Тут мы создаем свойство экзымпляра компонента this.loadProductTimer, а не состояния
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductTimer);
      this.loadProductTimer = setTimeout(() => {
        axios
          .get(`${API_BASE_URL}/api/products`, {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
              colorId: this.filterColorId,
            },
          })
          .then((response) => { this.productsData = response.data; })
          .catch(() => { this.productsLoadingFailed = true; })
          .then(() => { this.productsLoading = false; });
      }, 1000);
    },
  },
  watch: {
    // Наблюдаем за свойством page если изменилось то перезагружаем страницу
    page() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterColorId() {
      this.loadProducts();
    },
  },
  // Хук вызывет метод когда компонент создается
  created() {
    this.loadProducts();
  },
};
</script>
